body {
  background-color: #f4f4f4;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Georgia, Times New Roman, Times, serif;
  display: flex;
}

.form-container {
  background-color: #fff0;
  border-radius: 30px;
  width: 300px;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

label {
  margin-top: 10px;
  display: block;
}

input, textarea {
  border: 1px solid #000;
  border-radius: 15px;
  width: 100%;
  margin-top: 5px;
  padding: 8px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #28a745;
  border: none;
  border-radius: 4px;
  width: 100%;
  margin-top: 15px;
  padding: 10px;
}

button:hover {
  background-color: #0f1b11;
}

.hidden {
  color: green;
  text-align: center;
  margin-top: 20px;
  display: none;
}
